import React from 'react';
import {Container} from "@mui/material";

const Text = () => {
    return (
<Container>
    <div className='content-post-leatherhead '>
        <p className='style-global-text-info padding-bottom-10'>
            There, said the landlord, placing the candle on a crazy old sea chest that did double duty as a
            wash-stand and centre table; there, make yourself comfortable now,
            and good night to ye. I turned round from eyeing the bed, but he had disappeared.
        </p>

        <div className='list-articles'>
            <ul className='style-global-text-info'>
                <li>Why, afore we give it up, Sal used to put our <strong>Sam and little Johnny</strong> in the foot of it.</li>
                <li>But I got a dreaming and sprawling about one night, and somehow, Sam got pitched on the floor, and came near breaking his arm. Arter that, Sal said it wouldnt do.</li>
                <li>Come along here, Ill give ye a glim in a jiffy; and so saying he lighted a candle and held it towards me, offering to lead the way.</li>
                <li>But I stood irresolute; when looking at a clock in the corner, he exclaimed.</li>
            </ul>
        </div>
    </div>
</Container>
    );
};

export default Text;