import React from 'react';
import {Container} from "@mui/material";

const Text = () => {
    return (
        <Container>
            <div className="content-post-leatherhead">
                <p className="style-global-text-info padding-bottom-20">
                    How queer it seems, Alice said to herself, to be going messages for a rabbit!
                    I suppose Dinahll be sending me on messages next! And she began fancying the sort
                    of thing that would happen: Miss Alice! Come here directly, and get ready for your walk!
                    Coming in a minute, nurse! But Ive got to see that the mouse doesnt get out. Only I dont
                    think, Alice went on,
                    that theyd let Dinah stop in the house if it began ordering people about like that!
                </p>
                <p className='style-global-text-info  padding-bottom-20'>
                    By this time she had found her way into a tidy little room with a table in the window,
                    and on it (as she had hoped) a fan and two or three pairs of tiny white kid gloves: she
                    took up the fan and a pair of the gloves, and was just going to leave the room, when her
                    eye fell upon a little bottle that stood near the looking-glass. There was no label this
                    time with the words DRINK ME, but nevertheless she uncorked it and put it to her lips.
                    I know SOMETHING interesting is sure to happen, she said to herself, whenever I eat
                    or drink anything; so Ill just see what this bottle does. I do hope itll make me grow large again,
                    for really Im quite tired of being such a tiny little thing!
                </p>
                <div className="border-paragraph">
                    <p className="style-global-text-info">
                        By this time she had found her way into a tidy little room with a table in the window
                    </p>
                </div>
                <p className="style-global-text-info padding-top-20 padding-bottom">
                    By this time she had found her way into a tidy little room with a table in the window,
                    and on it (as she had hoped) a fan and two or three pairs of tiny white kid gloves:
                    she took up the fan and a pair of the gloves, and was just going to leave the room,
                    when her eye fell upon a little bottle that stood near the looking-glass. There was
                    no label this time with the words DRINK ME, but nevertheless she uncorked it and put
                    it to her lips. I know SOMETHING interesting is sure to happen, she said to herself,
                    whenever I eat or drink anything; so Ill just see what this bottle does. I do hope
                    itll make me grow large again, for really Im quite tired of being such a tiny little thing!
                </p>
                <p className="style-global-text-info">
                    It did so indeed, and much sooner than she had expected: before she
                    had drunk half the bottle, she found her head pressing against the ceiling,
                    and had to stoop to save her neck from being broken. She hastily put down the
                    bottle, saying to herself Thats quite enoughI hope I shant grow any moreAs it is,
                    I cant get out at the doorI do wish I hadnt drunk quite so much!
                </p>
            </div>
        </Container>
    );
};

export default Text;