import React from 'react';

import '../../../../Components/Style/Text-info.css'
import {Container} from "@mui/material";

const TextInfo = () => {
    return (
      <Container>
          <div className='content-text-info'>
              <p className='style-global-text-info'>I see nothing here, but a round thing made of gold,
                  and whoever raises a certain whale, this round
                  thing belongs to him. So, whats all this staring been about?
                  It is worth sixteen dollars, thats true; and at two cents the cigar,
                  thats nine hundred and sixty cigars.<br/><br/><br/>

                  I wont smoke dirty pipes like Stubb, but I like cigars,
                  and heres nine hundred and sixty of them; so here goes Flask aloft to spy em out.
                   What says the Cannibal? As I live hes comparing notes; looking at his thigh bone;
                  thinks the sun is in the thigh, or in the calf, or in the bowels, I suppose, as the old
                  women talk Surgeons Astronomy in the back country.
              </p>
          </div>
      </Container>
    );
};

export default TextInfo;